import React, {Component} from 'react';

class UserInfo extends Component{
    render(){
        return (
            <div>
                <p></p>
                <p>{this.props.username}</p>
                <p>{this.props.userInfo.username}</p>
            </div>
        )
    }
}

export default UserInfo;


